   .about{
background-color: #fff;
   }

  .container {
    margin-top: 20rem;
    width: 1200px;
    max-width: 1200px;
    margin: 0 auto;
  }

  .about {
    padding: 80px 0;
  }
  
  .p-25 {
    padding: 45px;
  }
  
  .row {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  .col-6 {
    width: 50%;
  }

  .about h3 {
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(2, 2, 2);
    font-size: 30px;
    margin-bottom: 12px;
    font-weight: 700;
  }
  
  .about h1 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 40px;
    color: #000000;
    font-weight: 700;
    line-height: 40px;
    margin-bottom: 15px;
  }
  .about p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    color: #000000;
    font-weight: 300;
    text-align: justify;
  }
  
  .about__btn {
    margin-top: 35px;
  }

  .about__img {
    margin-top: 1rem;
    width: 32rem;
    height: 30rem;
    border-radius: 100%;
    overflow: hidden;
    object-fit: cover;
  }
  
  .about__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  
  